@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body
{
    background-color: #E5E5E5;
    margin: 0;
}

#root
{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.containerApp
{
    width: 100%;
    height: 100%;
    background-color: transparent;
    max-width: 1032px;
}

.selectTab
{
    width: 100%;
    height: 100%;
    font-family: Roboto, sans-serif;
    font-size: 32px;
    margin: 0 0 50px 0;
}

.selectOuter
{
    margin: 0 0 50px 0;
}

.hideContent
{
    display: none;
}

.showContent
{
    height: 500px;
    width: 100%;
    box-sizing: border-box;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px 5% 5% 5%;
}

.containerButtonCheckout
{
    display: flex;
    width: 100%;
    height: 25%;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
}

.checkout,
.checkoutOff
{
    height: 48px;
    width: 200px;
    font-family: Roboto, sans-serif;
    color: #FFFFFF;
    border-radius: 80px;
    outline: none;
    border: 2px solid;
    background-color:  #215BF0;
    cursor: pointer;
    font-size: 16px;
}

.checkout:hover
{
    background-color: #0F41C4;
}

.checkoutOff
{
    cursor: default;
    background-color: #999999;
}


@media (max-width: 580px)
{
    .containerApp
    {
        width: 100%;
        height: 100%;
    }
}

@media (max-width: 320px)
{
    .containerApp
    {
        min-width: 320px;
    }
}

@media (min-width: 1200px)
{
    body
    {
        height: 100vh;
    }

    .containerApp
    {
        height: auto;
    }
}



.containerOuter
{
    width: 100%;
    height: 25%;
    display: flex;
    justify-content: space-between;
}

.containerInputName,
.containerInputNumber
{
    width: 100%;
    height: 100%;
    margin: 0 5% 0 0;
}

.containerInputNumber
{
    margin: 0 0 0 5%;
}

.containerInputAddress,
.containerInputComment
{
    width: 100%;
    height: 25%;
}

.inputText:focus,
.errorImage:focus,
.textArea:focus
{
    border: 1px solid black;
    outline:none;
}

.textArea
{
    border: 1px solid #999999;
    box-sizing: border-box;
    border-radius: 4px;
    height: 80px;
    width: 100%;
    margin: 5px 0 0 0;
    resize: none;
    outline:none;
    padding: 5px;
}

.showErrorText
{
    /*font-family: Roboto, sans-serif;*/
    color: red;
}

.hideErrorText
{
    display: none;
}

.inputText,
.errorImage
{
    border: 1px solid #999999;
    box-sizing: border-box;
    border-radius: 4px;
    height: 35px;
    width: 100%;
    padding: 5px;
    margin: 5px 0 0 0;
}

.errorImage
{
    background-image: URL(/static/media/error.f3478393.svg);
    background-repeat: no-repeat;
    background-position: right 5px bottom 45%;
    padding: 5px 30px 5px 5px;
}

.paddingLabel
{
    font-family: Roboto, sans-serif;
}

@media (max-width: 580px)
{
    .containerOuter
    {
        flex-direction: column;
        height: 50%;
    }

    .containerInputName
    {
        margin: 0;
    }

    .containerInputNumber
    {
        margin: 0;
    }
}









.marker
{
    width: 20px;
    height: 20px;
    position: absolute;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    background-image: URL(/static/media/marker.e09d33e2.svg);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    cursor: pointer;
}

.googleMapContainer
{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.containerTabs
{
    display: flex;
}

.tab
{
    width: 152px;
    height: 48px;
    margin: 0 8px 0 0;
    border-radius: 5px 5px 0 0;
    font-family: Roboto, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

.showTab
{
    cursor: default;
    background-color: #FFFFFF;
    color: #215BF0;
    margin: 0 8px 0 0;
}

.hideTab
{
    cursor: pointer;
    background-color: #EDEEEF;
    color: #999999;
    margin: 0 8px 0 0;
}

@media (max-width: 580px)
{
    .containerTabs
    {
        justify-content: space-between;
    }

    .showTab
    {
        margin: 0;
    }

    .hideTab
    {
        margin: 0;
    }

    .tab
    {
        width: 49%;
    }
}
.radioComponent
{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 40px 0 0;
}

.radioComponentOuter
{
    width: 100%;
    display: flex;
    margin: 30px 0 30px 0;
}

.addressPickup
{
    font-family: Roboto, sans-serif;
    font-size: 100%;
    line-height: 24px;
    cursor: pointer;
}

.addressPickup:hover
{
    color: #215BF0;
}


@media (max-width: 580px)
{
    .radioComponentOuter
    {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
    }

    .radioComponent
    {
        margin: 0 0 10px 0;
    }
}

